<template>
<div class="w-50">
  <div class="root">
    <span>{{text}}:</span>
    <el-select v-model="selectValInner"
      multiple
      placeholder="请选择">
      <el-option
        v-for="(item,index) in selectArr"
        :key="index"
        :value="item"
        :label="item">
      </el-option>
    </el-select>
  </div>
</div>
</template>
<script>
export default{
  props: {
    text: {
      type: String
    },
    selectVal: {
      required: true
    },
    selectArr: {
      type: Array,
      required: true
    }
  },
  data () {
    return {
      selectValInner: this.selectVal
    }
  },
  watch: {
    selectValInner (newVal) {
      // console.log('select Val :' + newVal)
      this.$emit('update', newVal)
    }
  }
}
</script>

<style scoped>
.root{
  display: flex;
  align-items: center;
  width: 450px;
  flex-shrink: 1;
  justify-content: space-between;
}
div /deep/ .el-select{
  width:350px;
  /* flex:auto; */
  flex-shrink: 1;
  flex-basis: auto;
  flex-grow: 0;
}
</style>



