// 范围选择器
const RANGE = 0
const TESTRANGE = -1
const TESTRANGETWO = -2
// 大于
const GREATE = 1
// 小于
const LESS = 2
// 大于等于
const GREATE_AND_EQUAL = 3
// 小于等于
const LESS_AND_EQUAL = 4
// 时间日期
const DATE_PICKER = 5
const DATE_PICKER_TEST = -5
// 文本框
const TEXT_INPUT = 6
// 数值框
const VALUE_INPUT = 7
// 下拉框
const DROPDOWN_INPUT = 8
// 模糊搜索
const SEARCH_INPUT = 9

// 数据类型
const VARCHAR = 10
const INT = 11

const isContianSpace = function (val) {
  return val.indexOf(' ') !== -1
}
const isValidInput = function (val) {
  if (isContianSpace(val)) {
    return false
  } else {
    if (val.indexOf('<') !== -1 || val.indexOf('>') !== -1 || val.indexOf('?') !== -1 || val.indexOf('"') !== -1 || val.indexOf("'") !== -1 || val.indexOf('\\') !== -1 || val.indexOf('/') !== -1 || val.indexOf('#') !== -1) {
      return false
    } else {
      return true
    }
  }
}
const isNum = function (val) {
  let reg = /^\d+$/
  return reg.test(val)
}
export default {
  RANGE,
  GREATE,
  LESS,
  GREATE_AND_EQUAL,
  LESS_AND_EQUAL,
  DATE_PICKER,
  TEXT_INPUT,
  VALUE_INPUT,
  DROPDOWN_INPUT,
  TESTRANGE,
  TESTRANGETWO,
  DATE_PICKER_TEST,
  SEARCH_INPUT,
  VARCHAR,
  INT,
  isContianSpace,
  isValidInput,
  isNum
}



