<template>
<span class="root">
  <template v-if="itemType == 0">
  <el-input-number v-model="leftNumInner" class="leftInputNumber" controls-position="right" @change="handleLeftChange" :min="ranges.leftMin" :max="ranges.leftMax" :size="size"></el-input-number>
  </template>
  <span class="tag" v-html="innerVal"></span>
  <el-input-number v-model="rightNumInner" controls-position="right" @change="handleRightChange" :min="ranges.rightMin" :max="ranges.rightMax" :size="size"></el-input-number>

</span>
</template>
<script>
// const RANGE = this.$store.state.RANGE
// const GREATE = this.$store.state.GREATE
// const LESS = this.$store.state.LESS
// const GREATE_AND_EQUAL = this.$store.state.GREATE_AND_EQUAL
// const LESS_AND_EQUAL = this.$store.state.LESS_AND_EQUAL

export default{
  props: {
    itemType: {
      type: Number,
      default: 0
    },
    text: {
      type: String,
      default: '金额'
    },
    leftNum: {
      type: Number,
      default: 0
    },
    rightNum: {
      type: Number
    },
    size: {
      type: String,
      default: ''
    },
    ranges: {
      type: Object,
      default: function () {
        return {
          leftMin: 0,
          leftMax: 10,
          rightMin: 15,
          rightMax: 100
        }
      }
    }
  },
  data () {
    return {
      leftNumInner: this.leftNum,
      rightNumInner: this.rightNum
    }
  },
  watch: {
    leftNumInner (newVal) {
      this.$emit('update', newVal, this.rightNumInner)
    },
    rightNumInner (newVal) {
      this.$emit('update', this.leftNumInner, newVal)
    }
  },
  methods: {
    // @todo:数值限定
    handleLeftChange () {
      // console.log('leftChange')
    },
    handleRightChange () {
      // console.log('rightChange')
    }
  },
  computed: {
    innerVal: function () {
      switch (this.itemType) {
        case this.$store.state.RANGE:
          return '≤&nbsp;' + this.text + '&nbsp;≤'
        case this.$store.state.GREATE:
          return '&nbsp;' + this.text + '&nbsp;<'
        case this.$store.state.LESS:
          return '&nbsp;' + this.text + '&nbsp;>'
        case this.$store.state.GREATE_AND_EQUAL:
          return '&nbsp;' + this.text + '&nbsp;≥'
        case this.$store.state.LESS_AND_EQUAL:
          return '&nbsp;' + this.text + '&nbsp;≤'
        default:
          return 'error'
      }
    }
  }
}
</script>
<style scoped>
.root{
  background-color: #ff4949;
}
.tag{
  border:none !important;
  outline:none !important;
  color: #ffffff;
  background-color: #ff4949;
  font-size: 12px;
}

</style>
<style scoped>
/*修改第三方库样式 */
div /deep/ .el-input-number__increase,div /deep/ .el-input-number__decrease{
  background-color: #ff4949;
  color: #ffffff;
  border-radius: 0 !important;
}
/*设置范围选择器颜色 */
div /deep/ .el-input-number__increase:hover,div /deep/ .el-input-number__decrease:hover {
  background-color: #ad3b31;
  color: #ffffff;
  border-radius: 0 !important;
}

div /deep/ .el-input.is-active .el-input__inner,div /deep/ .el-input__inner:focus,div /deep/ .el-input__inner:hover{
  border-color: #ff4949;
  border-radius: 0;
}
/*设置边框圆角 */
div /deep/ .el-input__inner{
  border-radius: 0;
}
div /deep/ .el-input-number__increase{
  border-radius: 0;
}

div /deep/ .el-input-number__decrease:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled), div /deep/  .el-input-number__increase:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled){
  border-color: #ff4949;
}

.leftInputNumber /deep/  .el-input-number__increase{
  left: 0px !important;
  right: auto;
}
.leftInputNumber /deep/  .el-input-number__decrease{
  left: 0px !important;
  right: auto;
}
.leftInputNumber /deep/  .el-input__inner{
  padding-left: 55px;
  padding-right: 10px;
}
.el-input-number{
  width: 100px;
}
</style>



