<template>
<div class="w-50">
  <div class="root">
    <span>{{text}}:</span>
    <el-input
      placeholder="请输入内容"
      v-model="inputInner"
      prefix-icon="el-icon-search"
      clearable
      @change="isNum"
    >
    </el-input>
  </div>
</div>
</template>
<script>
/*
params:
text
input
*/
export default{
// @todo :远程拉取数据做搜索？
  props: {
    text: {
      type: String
    },
    input: {
      required: true
    },
    dataType: {
      required: true
    }
  },
  data () {
    return {
      inputInner: this.input,
      isFirstListen: true
    }
  },
  watch: {
    inputInner (newVal, oldVal) {
      // console.log('input Inner is running ')

      if (!this.global.isValidInput(newVal)) {
        this.$message({
          message: '请确保输入不包含<、>、\\、/、?、#、空格等非法字符',
          center: true,
          type: 'warning'
        })
        newVal = oldVal
        this.inputInner = newVal
        // this.$emit('update', newVal)
        return
      }
      // 做校验和数据重组
      if (this.dataType === this.global.INT) {
        if (isNaN(newVal)) {
          newVal = oldVal
          this.inputInner = newVal
          this.$message({
            message: '请输入数字',
            center: true,
            type: 'warning'
          })
          // this.$emit('update', newVal)
        } else {
          newVal = parseInt(newVal)
          this.$emit('update', newVal)
        }
      } else if (this.dataType === this.global.VARCHAR) {
        this.$emit('update', newVal)
      }
    }
  },
  methods: {
    isNum (value) {
      if (this.global.isNum(value) && this.dataType === this.global.VARCHAR) {
        this.$message({
          message: '输入不能为纯数字',
          center: true,
          type: 'warning'
        })
        this.inputInner = null
        this.$emit('update', this.inputInner)
      }
    }
  }
}
</script>

<style scoped>
.el-input{
  width: 350px;
  /* flex:auto; */
  flex-shrink: 1;
  flex-basis: auto;
  flex-grow: 0;
}
.root{
  display: flex;
  align-items: center;
  width: 450px;
  flex-shrink: 1;
  justify-content: space-between;
}
</style>



