import Vue from 'vue'
import 'es6-promise/auto'
import Vuex from 'vuex'
import {getToken} from '@/request/token'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    userId: '',
    token: getToken(),
    network: true
  },
  mutations: {
    SET_TOKEN: (state, token) => {
      state.token = token
    },
    SET_NETWORK: (state, status) => {
      state.network = status
    },
    SET_USERID: (state, status) => {
      state.userId = status
    }
  },
  actions: {
  }
})



