<template>
<div class="w-50">
  <div class="root">
    <span>{{text}}:</span>
    <el-select v-model="selectValInner"
      multiple
      placeholder="请输入内容，按enter键确定"
      prefix-icon="el-icon-edit"
      popper-class="hide"
      clearable
      :popper-append-to-body="false"
      filterable
      allow-create
      default-first-option
      >
    </el-select>
  </div>
</div>
</template>
<script>
/*
params:
text
input
*/
export default{
  props: {
    text: {
      type: String
    },
    selectVal: {
      required: true
    },
    dataType: {
      required: true
    }
  },
  data () {
    return {
      selectValInner: this.selectVal,
      isFirstListen: true
    }
  },
  watch: {
    selectValInner (newVal, oldVal) {
      if (this.isFirstListen) {
        this.isFirstListen = false
        return
      }
      // console.log('select Val :' + newVal)
      if (!this.global.isValidInput(newVal[newVal.length - 1])) {
        this.$message({
          message: '请确保输入不包含<、>、\\、/、?、#、空格等非法字符',
          center: true,
          type: 'warning'
        })
        newVal = oldVal
        this.selectValInner = newVal
        this.$emit('update', newVal)
        return
      }
      // 做校验和数据重组
      if (this.dataType === this.global.INT) {
        if (isNaN(newVal[newVal.length - 1])) {
          // console.log('before : ---- newVal : ' + newVal + ' oldVal : ' + oldVal)
          newVal = oldVal
          this.selectValInner = newVal
          // console.log('after : ---- newVal : ' + newVal + ' oldVal : ' + oldVal)
          this.$message({
            message: '请输入数字',
            center: true,
            type: 'warning'
          })
          this.$emit('update', newVal)
        } else {
          for (let i = 0; i < newVal.length; i++) {
            newVal[i] = parseInt(newVal[i])
          }
          this.$emit('update', newVal)
        }
      } else if (this.dataType === this.global.VARCHAR) {
        if (!isNaN(newVal[newVal.length - 1])) {
          // console.log('before : ---- newVal : ' + newVal + ' oldVal : ' + oldVal)
          newVal = oldVal
          this.selectValInner = newVal
          // console.log('after : ---- newVal : ' + newVal + ' oldVal : ' + oldVal)
          this.$message({
            message: '请输入字符',
            center: true,
            type: 'warning'
          })
          this.$emit('update', newVal)
        } else {
          this.$emit('update', newVal)
        }
      }
    }
  }
}
</script>

<style scoped>
.root{
  display: flex;
  align-items: center;
  width: 450px;
  flex-shrink: 1;
  justify-content: space-between;
}
div /deep/ .el-select{
  width:350px;
  /* flex:auto; */
  flex-shrink: 1;
  flex-basis: auto;
  flex-grow: 0;
}
div /deep/ .el-input__suffix{
  display: none;
}
</style>
<style>
.hide{
  display: none !important;
}
</style>



