<template>
<div class="root">
  <el-input-number v-model="leftNumInner" class="leftInputNumber" controls-position="right" @input="handleLeftChange" :min="min" :max="rightNumInner" :size="size"></el-input-number>
  <span class="tag" v-html="innerVal"></span>
  <el-input-number v-model="rightNumInner" controls-position="right" @input="handleRightChange" :min="leftNumInner" :max="max" :size="size"></el-input-number>
</div>
</template>
<script>
/*
左值和右值
leftNum,rightNum
size控制大小
min:下界
max:上界
*/
export default{
  props: {
    text: {
      type: String,
      required: true
    },
    leftNum: {
      type: Number,
      required: true
    },
    rightNum: {
      type: Number,
      required: true
    },
    size: {
      type: String,
      default: 'small'
    },
    min: {
      type: Number,
      required: true
    },
    max: {
      type: Number,
      required: true
    }
  },
  data () {
    return {
      leftNumInner: this.leftNum,
      rightNumInner: this.rightNum
    }
  },
  watch: {
    leftNumInner (newVal) {
      this.$emit('update', newVal, this.rightNumInner)
    },
    rightNumInner (newVal) {
      this.$emit('update', this.leftNumInner, newVal)
    }
  },
  methods: {
    handleLeftChange () {
      console.log('leftChange')
    },
    handleRightChange () {
      console.log('rightChange')
    }
  },
  computed: {
    innerVal: function () {
      // '≤&nbsp;' + this.text + '&nbsp;≤'
      let html = `<i class="icon-el-icon-lne-2 iconfont"></i>&nbsp;` + this.text + `&nbsp;<i class="icon-el-icon-lne-2 iconfont"></i>`
      return html
    }
  }
}
</script>
<style scoped>
.root{
  background-color: #ff4949;
  width: 300px;
  display: flex;
  align-items: center;
}
.tag{
  color: #ffffff;
  background-color: #ff4949;
  font-size: 14px;
  text-align: center;
  width: 120px;
}

</style>
<style scoped>
/*修改第三方库样式 */
div /deep/ .el-input-number__increase,div /deep/ .el-input-number__decrease{
  background-color: #ff4949;
  color: #ffffff;
  border-radius: 0 !important;
}
/*设置范围选择器颜色 */
div /deep/ .el-input-number__increase:hover,div /deep/ .el-input-number__decrease:hover {
  background-color: #F56C6C;
  color: #ffffff;
  border-radius: 0 !important;
}
div /deep/ .el-input__inner{
  border-color: #ff4949;
}
/* div /deep/ .el-input.is-active .el-input__inner,div /deep/ .el-input__inner:focus,div /deep/ .el-input__inner:hover{
  border-color: #ff4949;
  border-radius: 0;
} */
/*设置边框圆角 */
div /deep/ .el-input__inner{
  border-radius: 0;
}
div /deep/ .el-input-number__increase{
  border-radius: 0;
}
.leftInputNumber /deep/  .el-input-number__increase,.leftInputNumber /deep/  .el-input-number__decrease{
  left: 0px !important;
  right: auto;
}
.leftInputNumber /deep/  .el-input__inner{
  padding-left: 50px;
  padding-right: 15px;
}
.el-input-number{
  width: 90px;
}
</style>



