<template>
<span>
{{text}}&nbsp;&nbsp;
<el-input-number :size="size" v-model="valueInner"></el-input-number>
</span>
</template>
<script>
export default{
  props: {
    text: {
      type: String,
      default: '数值选择器'
    },
    size: {
      type: String
    },
    value: {
      type: Number
    }
  },
  data () {
    return {
      valueInner: this.value
    }
  },
  watch: {
    valueInner (newVal) {
      this.$emit('update', newVal)
    }
  }
}
</script>
<style scoped>
div /deep/ .el-input-number__increase,div /deep/ .el-input-number__decrease{
  background-color: #ff4949;
  color: #ffffff;
  border-radius: 0 !important;
}
/*设置范围选择器颜色 */
div /deep/ .el-input-number__increase:hover,div /deep/ .el-input-number__decrease:hover {
  background-color: #ad3b31;
  color: #ffffff;
  border-radius: 0 !important;
}

div /deep/ .el-input.is-active .el-input__inner,div /deep/ .el-input__inner:focus,div /deep/ .el-input__inner:hover{
  border-color: #ff4949;
  border-radius: 0;
}
/*设置边框圆角 */
div /deep/ .el-input__inner{
  border-radius: 0;
}
div /deep/ .el-input-number__increase{
  border-radius: 0;
}
div /deep/ .el-input-number__decrease:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled), div /deep/  .el-input-number__increase:hover:not(.is-disabled)~.el-input .el-input__inner:not(.is-disabled){
  border-color: #ff4949;
}
.el-input-number{
  width: 150px;
}
</style>



