<template>
<div class="w-50">
  <div class="root">
    <span>{{text}}:</span>
    <span>
      <el-input placeholder="最小值" v-model="leftNumInner" :size="size" @change="isNum(leftNumInner,'left')"></el-input>
      <el-divider></el-divider>
      <el-input placeholder="最大值" v-model="rightNumInner" :size="size" @change="isNum(rightNumInner,'right')"></el-input>
    </span>
  </div>
</div>
</template>
<script>
/*
左值和右值
leftNum,rightNum
size控制大小
*/
export default{
  props: {
    text: {
      type: String,
      required: true
    },
    leftNum: {
      required: true
    },
    rightNum: {
      required: true
    },
    size: {
      type: String,
      default: ''
    },
    dataType: {
      required: true
    }
  },
  data () {
    return {
      leftNumInner: this.leftNum,
      rightNumInner: this.rightNum
    }
  },
  watch: {
    leftNumInner (newVal, oldVal) {
      if (!this.global.isValidInput(newVal)) {
        this.$message({
          message: '请确保输入不包含<、>、\\、/、?、#、空格等非法字符',
          center: true,
          type: 'warning'
        })
        newVal = oldVal
        this.leftNumInner = newVal
        this.$emit('update', newVal, this.rightNumInner)
        return
      }
      // 做校验和数据重组
      if (this.dataType === this.global.INT) {
        if (isNaN(newVal)) {
          newVal = oldVal
          this.leftNumInner = newVal
          this.$message({
            message: '请输入数字',
            center: true,
            type: 'warning'
          })
        } else {
          newVal = parseInt(newVal)
          this.$emit('update', newVal, this.rightNumInner)
        }
      } else if (this.dataType === this.global.VARCHAR) {
        this.$emit('update', newVal, this.rightNumInner)
      }
    },
    rightNumInner (newVal, oldVal) {
      if (!this.global.isValidInput(newVal)) {
        this.$message({
          message: '请确保输入不包含<、>、\\、/、?、#、空格等非法字符',
          center: true,
          type: 'warning'
        })
        newVal = oldVal
        this.rightNumInner = newVal
        this.$emit('update', this.leftNumInner, newVal)
        return
      }
      // 做校验和数据重组
      if (this.dataType === this.global.INT) {
        if (isNaN(newVal)) {
          newVal = oldVal
          this.rightNumInner = newVal
          this.$message({
            message: '请输入数字',
            center: true,
            type: 'warning'
          })
        } else {
          newVal = parseInt(newVal)
          this.$emit('update', this.leftNumInner, newVal)
        }
      } else if (this.dataType === this.global.VARCHAR) {
        this.$emit('update', this.leftNumInner, newVal)
      }
    }
  },
  methods: {
    isNum (value, str) {
      if (this.global.isNum(value) && this.dataType === this.global.VARCHAR) {
        this.$message({
          message: '输入不能为纯数字',
          center: true,
          type: 'warning'
        })
        // value = null
        if (str === 'left') {
          this.leftNumInner = null
          console.log('this.leftNum: running ')
          this.$emit('update', this.leftNumInner, this.rightNumInner)
        } else if (str === 'right') {
          this.rightNumInner = null
          console.log('this.rightNum: running ')
          this.$emit('update', this.leftNumInner, this.rightNumInner)
        }
        console.log('isNum method: running')
      }
    }
  },
  computed: {
  }
}
</script>
<style scoped>
.w-50{
  width:50%;
  display:flex;
}
.root{
  display: flex;
  align-items: center;
  width:450px;
  flex-shrink: 1;
  justify-content: space-between;
}
.root > span{
  display: flex;
  align-items: center;
  justify-content: flex-start;
}
.el-input{
  /* flex:auto; */
  width: 130px;
  flex-shrink: 1;
  flex-basis: auto;
  flex-grow: 0;
}
.el-divider{
  width: 90px;
  flex-shrink: 1;
  flex-basis: auto;
  flex-grow: 0;
}
</style>



