// import base from './base' // 导入接口域名列表
import axios from '@/request/http' // 导入http中创建的axios实例
// 根据筛选条件，发送筛选请求给后端
const download = {
  getZipFile (url) {
    axios({
      method: 'GET',
      url: url,
      responseType: 'blob'
    })
  }
}

export default download



