<template>
<!--
@params
------------text
------------dataDateInner
-->
<div class="item-flex item-w-300">
  {{text}}:&nbsp;&nbsp;&nbsp;&nbsp;
  <el-date-picker
    v-model="dataDateInner"
    type="date"
    placeholder="选择日期"
    format="yyyy 年 mm 月 dd 日"
    value-format="yyyy-MM-dd"
    :size="size"
  >
  </el-date-picker>
</div>
</template>
<script>
export default ({
  props: {
    // @Require?
    text: {
      type: String,
      default: '数据日期'
    },
    dataDate: {
      default: ''
    },
    size: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      dataDateInner: this.dataDate
    }
  },
  watch: {
    dataDateInner (newVal) {
      // console.log('newVal 0' + newVal[0])
      // console.log('newVal 1' + newVal[1])
      this.$emit('update', newVal)
    }
  }
})
</script>
<style scoped>
div /deep/ .el-date-editor{
  flex: auto;
  width: 185px;
}
div /deep/ .el-input__inner{
  padding-right: 0px;
}
</style>



