<template>
<div class="w-50">
  <div class="root">
    <span>{{text}}:&nbsp;&nbsp;&nbsp;&nbsp;</span>
    <el-input placeholder="最小值" v-model="leftNumInner" :size="size"></el-input>
    <el-divider></el-divider>
    <el-input placeholder="最大值" v-model="rightNumInner" :size="size"></el-input>
  </div>
</div>
</template>
<script>
/*
左值和右值
leftNum,rightNum
size控制大小
*/
export default{
  props: {
    text: {
      type: String,
      required: true
    },
    leftNum: {
      type: Number,
      required: true
    },
    rightNum: {
      type: Number,
      required: true
    },
    size: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      leftNumInner: this.leftNum,
      rightNumInner: this.rightNum
    }
  },
  watch: {
    leftNumInner (newVal) {
      this.$emit('update', newVal, this.rightNumInner)
    },
    rightNumInner (newVal) {
      this.$emit('update', this.leftNumInner, newVal)
    }
  },
  methods: {
  },
  computed: {
  }
}
</script>
<style scoped>
.w-50{
  width:50%;
  display:flex;
}
.root{
  display: flex;
  align-items: center;
  width:450px;
  flex-shrink: 1;
}
.el-input{
  flex:auto;
  width: 100px;
}
.el-divider{
  width: 60px;
}
</style>



