// import base from './base' // 导入接口域名列表
import axios from '@/request/http' // 导入http中创建的axios实例
import qs from 'qs'
// 根据筛选条件，发送筛选请求给后端
const service = {
  // getScreenData () {
  //   return axios.get(`${base.sq}`)
  // },
  getServiceByGet (url, params) {
    // 获取数据
    console.log('getServiceByGet Running')
    return axios.get(url, {
      params: params
    })
  },
  getServicePost (url, params) {
    // 获取数据
    console.log('getServicePost Running')
    return axios.post(url, qs.stringify(params))
  }
}

export default service



