<template>
<!--
@参数说明：
text指定日期前文本
dataDate
-->
<div class="w-50">
  <div class="root">
    <span>{{text}}:</span>
    <el-date-picker
      v-model="dataDateInner"
      type="daterange"
      range-separator="至"
      start-placeholder="开始日期"
      end-placeholder="结束日期"
      :size="size"
      unlink-panels
      :picker-options="pickerOptions"
      align="center"
    >
    </el-date-picker>
  </div>
</div>
</template>
<script>
export default ({
  props: {
    // @Require?
    text: {
      type: String
    },
    dataDate: {
      type: Array
    },
    size: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      dataDateInner: this.dataDate,
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近一个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '最近三个月',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '某天之前',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            start.setFullYear(1984, 0, 1)
            picker.$emit('pick', [start, end])
          }
        }, {
          text: '某天之后',
          onClick (picker) {
            const end = new Date()
            const start = new Date()
            end.setFullYear(9999, 11, 31)
            picker.$emit('pick', [start, end])
          }
        }
        ]
      }
    }
  },
  watch: {
    dataDateInner (newVal) {
      // console.log('newVal 0' + newVal[0])
      // console.log('newVal 1' + newVal[1])
      this.$emit('update', newVal[0], newVal[1])
    }
  }
})
</script>
<style scoped>
.w-50{
  width:50%;
  display:flex;
}
.root{
  width: 450px;
  display: flex;
  align-items: center;
  flex-shrink: 1;
  justify-content: space-between;
}
.el-date-editor{
  width: 350px;
  /* flex:auto; */
  flex-shrink: 1;
  flex-basis: auto;
  flex-grow: 0;
}
</style>



