      })
    },
    // 根据后端数据，计算要渲染的组件类型
    computeComponent (type) {
      switch (type) {
        case this.global.TESTRANGETWO:
          return 'RangeSelectorItemTestTwo'
        case this.global.TESTRANGE:
          return 'RangeSelectorItemTest'
        case this.global.RANGE:
          return 'RangeSelectorItem'
        case this.global.GREATE:
        case this.global.LESS:
        case this.global.GREATE_AND_EQUAL:
        case this.global.LESS_AND_EQUAL:
          return 'InequalityItem'
        // 5
        case this.global.DATE_PICKER:
          return 'DateSelectorItem'
        case this.global.DATE_PICKER_TEST:
          return 'DateSelectorItemTest'
        // 6
        case this.global.TEXT_INPUT:
          return 'InputTextItem'
        // 7
        case this.global.VALUE_INPUT:
          return 'NumberItem'
        // 8
        case this.global.DROPDOWN_INPUT:
          return 'DropDownItem'
        // 模糊搜素
        case this.global.SEARCH_INPUT:
          return 'SearchItem'
      }
    },
    // 该函数用于解决筛选子组件无法向父组件传参问题
    updateData (args, index, type) {
      // @todo: 提炼出来一个方法，专门判断
      switch (type) {
        case this.global.RANGE:
          this.dynamicComponents[index].data.leftNum = args[0]
          this.dynamicComponents[index].data.rightNum = args[1]
          break
        case this.global.TESTRANGE:
          this.dynamicComponents[index].data.leftNum = args[0]
          this.dynamicComponents[index].data.rightNum = args[1]
          break
        case this.global.TESTRANGETWO:
          // console.log('args[0].:' + typeof (args[0]))
          this.dynamicComponents[index].data.value[0] = args[0]
          this.dynamicComponents[index].data.value[1] = args[1]
          break
        case this.global.GREATE:
        case this.global.LESS:
        case this.global.GREATE_AND_EQUAL:
        case this.global.LESS_AND_EQUAL:
          this.dynamicComponents[index].data.leftNum = args[0]
          this.dynamicComponents[index].data.rightNum = args[1]
          break
        // 5
        case this.global.DATE_PICKER:
          // console.log('DATE_PICKER detecting...')
          this.dynamicComponents[index].data.dataDate[0] = args[0]
          this.dynamicComponents[index].data.dataDate[1] = args[1]
          break
        case this.global.DATE_PICKER_TEST:
          // console.log('DATE_PICKER detecting...')
          this.dynamicComponents[index].data.dataDate = args[0]
          break
        // 6
        case this.global.TEXT_INPUT:
          this.dynamicComponents[index].data.selectVal = args[0]
          break
        case this.global.SEARCH_INPUT:
          this.dynamicComponents[index].data.input = args[0]
          break
        // 7
        case this.global.VALUE_INPUT:
          this.dynamicComponents[index].data.value = args[0]
          break
        // 8
        case this.global.DROPDOWN_INPUT:
          this.dynamicComponents[index].data.selectVal = args[0]
          break
      }
    },
    // 该函数用于组织好用户的筛选条件，返回对象数组
    getSelectedParams () {
      let params = []
      let paramsArr = this.dynamicComponents
      let data = null
      // 封装筛选数据
      for (let i = 0; i < paramsArr.length; i++) {
        // 组织参数
        // @todo: 这里可能要做更多细致的类别筛选
        if (paramsArr[i].type === this.global.RANGE) {
          data = {
            leftNum: paramsArr[i].data.leftNum,
            rightNum: paramsArr[i].data.rightNum
          }
        } else if (paramsArr[i].type === this.global.TESTRANGE) {
          data = {
            leftNum: paramsArr[i].data.leftNum,
            rightNum: paramsArr[i].data.rightNum
          }
        } else if (paramsArr[i].type === this.global.TESTRANGETWO) {
          data = {
            value: [paramsArr[i].data.value[0], paramsArr[i].data.value[1]]
          }
        } else if (paramsArr[i].type === this.global.DATE_PICKER) {
          data = paramsArr[i].data.dataDate
        } else if (paramsArr[i].type === this.global.DATE_PICKER_TEST) {
          data = paramsArr[i].data.dataDate
        } else if (paramsArr[i].type === this.global.DROPDOWN_INPUT) {
          data = paramsArr[i].data.selectVal
        } else if (paramsArr[i].type === this.global.TEXT_INPUT) {
          data = paramsArr[i].data.selectVal
        } else if (paramsArr[i].type === this.global.SEARCH_INPUT) {
          data = paramsArr[i].data.input
        }

        if (paramsArr[i].type === this.global.GREATE || paramsArr[i].type === this.global.LESS || paramsArr[i].type === this.global.GREATE_AND_EQUAL || paramsArr[i].type === this.global.LESS_AND_EQUAL) {
          data = {
            leftNum: paramsArr[i].data.leftNum,
            rightNum: paramsArr[i].data.rightNum
          }
        } else if (paramsArr[i].type === this.global.VALUE_INPUT) {
          data = paramsArr[i].data.value
        }
        // console.log('变更后的数据: ' + data)
        params.push({
          // 添加多一个标识符号，比如name
          type: paramsArr[i].type,
          data: data
        })
      }
      return params
    },
    // 用户点击生成数据任务，则会通过该函数，发送筛选条件给后台，由后台根据筛选条件完成数据的打包工作
    generateDataTask () {
      // console.log('generate Data Task')
      let params = this.getSelectedParams()
      let url = 'https://www.112233344.com/xxxx'
      // 切换操作状态
      this.currentState = '打包中'
      this.generateAble = false
      // 发送数据，根据后端的返回
      this.api.generateTask.sendScreenDataByPost(url, params).then(response => {
        /*
        根据返回数据，设置操作状态和下载按钮的可用性
        */
        this.downloadUrl = response.data.downloadUrl
        this.downloadAble = true
        this.currentState = '可下载'
      }).catch(() => {
        // 错误处理
        // @todo:添加错误代码？
        this.$message.error('数据打包失败!')
        this.currentState = '无数据任务'
      })
    },
    // 用户点击筛选按钮触发函数，负责获取组件数据，传递给后端，获取到后端的数据渲染到表格上
    selectData () {
      // console.log('select Data')
      let url = 'https://www.112233344.com/xxxx'
      let params = this.getSelectedParams()
      // 发送请求
      this.api.screen.getScreenDataByPost(url, params).then(response => {
        // 将返回结果渲染到tableList上
        // console.log('promise test here : ' + response)
        this.dataNum = response.data.dataNum
        this.selectedTableData = response.data.selectedTableData
        this.selectedTableShow = true
        this.generateAble = true
      }).catch((err) => {
        console.log('promise test : ' + err)
        this.$message.error('数据筛选失败!')
        // @实际开发无此行
        this.selectedTableShow = true
      })
    },
    // 向相应的URL发送下载请求
    download () {
      console.log('download')
      // ZIP文件下载
      this.api.download.getZipFile(this.downloadUrl).then(response => {
        if (response.data.type === 'application/octet-stream') {
          // 获取http头部的文件名信息，若无需重命名文件，将下面这行删去
          const fileName = response.headers['content-disposition'].split('=')[1]
          /* 兼容ie内核，360浏览器的兼容模式 */
          if (window.navigator && window.navigator.msSaveOrOpenBlob) {
            const blob = new Blob([response.data], { type: 'application/zip' })
            window.navigator.msSaveOrOpenBlob(blob, fileName)
          } else {
            /* 火狐谷歌的文件下载方式 */
            const blob = new Blob([response.data], { type: 'application/zip' })
            const url = window.URL.createObjectURL(blob)
            const link = document.createElement('a') // 创建a标签
            link.href = url
            link.download = fileName // 文件重命名，若无需重命名，将该行删去
            link.click()
            URL.revokeObjectURL(url) // 释放内存
          }
          // resolve(response)
          // 下载完毕，更新状态
          this.currentState = '下载成功'
          // 延迟1秒，更新状态
          setTimeout(() => {
            this.currentState = '无数据任务'
          }, 1000)
        } else {
          // 错误处理
          this.$message.error('文件下载异常')
        }
      }).catch(() => {
        this.$message.error('文件下载异常')
      })
    },
    getFormatDate (date) {
      return this.utils.getFormatDate(date)
    },
    isNum (val) {
      let reg = /^\d+$/
      return reg.test(val)
    }
  },
