}
// @todo:size大小的统一
// @todo:响应式布局,兼容性设置font
</script>

<style scoped>
.card{
  /*@todo: 指定多个字体 */
  font-family: 'Microsoft YaHei';
  font-size: 16px;
  line-height: 1.5;
  width: 1000px;
  padding:10px;
  background-color: rgb(252, 246, 246);
}
@media screen and (max-width: 1000px) {
  .card{
    width: 1000px;
  }
}
.card > div{
  background: #fff;
  padding:12px;
  margin-top:10px;
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  border-radius: 4px;
}
.card > div:hover{
  box-shadow: 0 2px 4px rgba(0, 0, 0, .12), 0 0 6px rgba(0, 0, 0, .04);
}
.card > div:first-child{
  margin-top: 0px;
}

.title{
  margin-top:0px;
  /* @底部可能有Bug */
  margin-bottom:12px;
}
.flex-box{
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
}

.serviceIntro > div,.specificFunc > div{
  margin-top: 16px;
}

.service-intro-line>span{
  width:50%;
