}
.serviceRecord{
  display: flex;
  align-items: center;
}
.specificFuncInfo{
  margin-bottom: 12px;
}
.el-divider{
  margin-bottom: 12px !important;
}
.filter-div{
  margin-top: 0px !important;
  margin-bottom: -16px;
}
.filter-div > *{
  margin-bottom: 24px;
}
.filter-div{
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

/* 通用的写进一个统一的样式文件里 */
.background-light-red{
  background-color: #d42f2f;
}
.background-primary-red{
  background-color: #AF362C;
}
.text-white{
  color: #ffffff;
}

.btn-light-red,.btn-light-red:focus{
  background-color: #d42f2f;
  color: #ffffff;
}
.btn-light-red:hover{
  background-color: #ff6262;
  color:#ffffff;
}
.btn-primary-red,.btn-primary-red:focus{
  background-color: #AF362C;
  color: #ffffff;
}
.btn-primary-red:hover{
  background-color: #AF362C;
  color:#ffffff;
}
.btn-bold{
  font-weight: 700;
}
.table-fit-content{
  width: fit-content;
  width: -moz-fit-content;
}
/* 设置表格样式 */
div /deep/ .el-table th:hover{
  cursor: pointer;
}
div /deep/ .el-table__body tr:hover>td{
  background-color: #ff6262!important;
  color: #ffffff;
  cursor: pointer;
}
div /deep/ .el-table__body tr.current-row>td{
  background-color: #ff6262!important;
  cursor: pointer;
  color: #ffffff;
}
.dataNumAndGenerateTask{
  width: 450px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.currentStateAndDownload{
  align-items: center;
  justify-content: space-between;
}
</style>
<style scoped>
.fs-16{
  font-size:  16px !important;
}
.fw-700{
  font-weight: 700 !important;
}
.primary-red{
  color: #d42f2f;
}
.float-right{
  float:right;
}
.clearfix{
  clear:both;
}
</style>



